import Layout from '../../components/Layout'
import Member from '../../components/Member'
import Link from 'next/link'
import {getProfileAndJournal} from '../../actions/member'
import {useEffect,useState} from 'react'

const MemberDashboard = ({member,journals}) => {






	return (
		<React.Fragment>
		<Layout>
			<Member>
				<div className="container-fluid">

					<div className="row">
						<div className="col-md-12">
							<div className="card">
								<div className="card-body">
									<div className="row">
										<div className="col-md-8">
											<h5>Username</h5>
											<p className="test-muted">User joined at Date</p>
										</div>
										<div className="col-md-4">
											Image Profile
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
					<br/>

					<div className="pb-5">
					<div className="row">
						<div className="col-md-6">
							<div className="card">
								<div className="card-body">
									<h5 className="card-title bg primary pt-4 pb-4 pl-4 pr-4">Recent journals</h5>
								</div>
							</div>
						</div>
						<div className="col-md-6">
							<div className="card">
								<div className="card-body">
									<h5 className="card-title bg primary pt-4 pb-4 pl-4 pr-4 ">Statistics</h5>
								</div>
							</div>
						</div>
					</div>
					</div>




					<div className="row">
						<div className="col-md-12 pt-5 pb-5">
							<h2>Member Dashboard</h2>
						</div>
						<div className="col-md-6">
							<ul className="list-group">

								<li className="list-group-item">
		  							<Link href="/member/update">
		  								<a>Update Profile</a>
		  							</Link>
	  							</li>

	  							<li className="list-group-item">
		  							<Link href="/member/journal">
		  								<a>Create Journal</a>
		  							</Link>
	  							</li>

	  							<li className="list-group-item">
		  							<Link href="/member/journal-manage">
		  								<a>Update/Delete Journal</a>
		  							</Link>
	  							</li>

							</ul>
						</div>
						<div className="col-md-6">
							right
						</div>
					</div>
				</div>
			</Member>
		</Layout> 
		</React.Fragment>
	)
}


export default MemberDashboard;